import React from "react";
import {Col} from "react-bootstrap";
import {ApplicationDesktopButton} from "./ApplicationDesktopButton";

export const ApplicationDesktopButtons = () => {
	return(
		<Col className='col-12 col-md-5 col-xl-5 desktop__buttons-wrapper'>
			<ApplicationDesktopButton name='przepis' direction='/recipes'/>
			<ApplicationDesktopButton name='plan' direction={'/schedule'}/>
			<ApplicationDesktopButton name='zakupy' direction='/schedule'/>
		</Col>
	)
}